.news-box-with-title-content{
    -ms-flex-positive: 1;
        flex-grow: 1;
    width: 320px;
    overflow: hidden;
    position: relative;
}
.news-box-with-title-content>div{
    width: 1600px;
    position: absolute;
    height: 240px;
    left: 0;
    top:0;
    -webkit-transition: left .3s ease-out;
    -o-transition: left .3s ease-out;
    transition: left .3s ease-out;
}


